@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#app,
#app > div {
  width: 100%;
}

#red {
  color: red;
}

.wholePage {
  text-align: center;
}

button,
input {
  margin-top: 10px;
  font-size: 0.8em;
}

.listItem {
  line-height: 50px;
}

/* Adapted from https://unpkg.com/react-tabs@5.1.0/style/react-tabs.css */

.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  min-width: 220px;
  /* center it vertically within the iframe */
  margin-top: calc((830px - 285px) / 2);
  background-color: white;
  border: 1px solid #aaa;
  margin-right: 1em;
  margin-bottom: 1em;
  overflow: hidden;
  list-style-position: inside;
  padding-left: 0;
  float: left;
  position: fixed;
  font-size: 1.1rem;
}

.react-tabs__tab {
  display: block;
  border: 1px solid #ddd;
  position: relative;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
  /* Make the text unselectable (via https://stackoverflow.com/a/15273197) */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
}

/* the "Job Description" tab (first Tab in the TabList) */
.react-tabs__tab-list > .react-tabs__tab:first-child {
  border-bottom: 2px solid;
  font-style: italic;
}

.react-tabs__tab--selected {
  background-color: #ccc;
  border-color: black;
  font-weight: bold;
}

.react-tabs__tab:focus {
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: white;
  display: none;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

/* ############################## Big-Picture Positioning ############################# */
#root {
  height: 100%;
  min-height: 100vh;
  font-size: 20px;
}

.App {
  font-family: "Rubik", sans-serif;
  display: flex;
  justify-content: center;
}

.pool {
  width: 100%;
}

/* Match react-tabs */
.card-body {
  margin-left: calc(max(17%, 235px) + 20px);
}

/* ############################## Content of a Tab ############################# */
.candidate,
.jobDescription {
  width: 100%;
  margin-bottom: 3%;
  background-color: white;
  text-align: start;
  padding-top: 3%;
}

.candidate h1,
.jobDescription h1 {
  font-weight: bolder;
}

.candidate h2 {
  text-decoration: underline;
  margin-bottom: 2rem;
}

.candidate h3,
.jobDescription h2 {
  font-weight: bold;
  margin-top: 2rem;
}

.candidate h4 {
  font-size: inherit;
}

.candidate h5 {
  color: #888;
  font-size: 1.1rem;
}

.candidate .eduBasics {
  margin-bottom: 0;
}

.candidate .eduInfo {
  color: #888;
  font-size: 1.1rem;
  margin-left: 2rem;
  margin-top: 0;
}

